import "./consiglio.js";
import { StringRenderer } from "../../commons/js/string_renderer.js";

$(document).ready(function () {

    var url_to_call = apibase.baseurl + site_config.siteID + "/" + site_config.typeID_header_footer;
    $.ajax({
        url: url_to_call, success: function (result) {
            if (result != null && result.components != undefined) {

                let linkFasciaUno = []
                if (result.components.link_fascia_uno != undefined) {
                    linkFasciaUno = result.components.link_fascia_uno.components;

                }

                var fasciaUnoHtml = "";
                if (linkFasciaUno.length > 0) {
                    fasciaUnoHtml = " <div class=\"row justify-content-around\">\n";
                }
                linkFasciaUno.forEach(function (element) {
                    var iconLink = "";
                    if (element.properties.icon.indexOf("it-") != -1) {
                        iconLink = "svg/sprite.svg#" + element.properties.icon;
                    }
                    if (element.properties.icon.indexOf("ba-") != -1) {
                        iconLink = "svg/ba-sprite.svg#" + element.properties.icon;
                    }
                    var urlParams = new URLSearchParams(element.properties.url);

                    if (urlParams.has("section_id")) {
                        localStorage.setItem(urlParams.get("section_id") + "label", element.title);

                    }
                    fasciaUnoHtml += "<div class=\"col text-center align-content-center\"> <a href=\"" + element.properties.url + "\" class=\"text-white\"><div class =\"box-fascia-1 text-center\"><svg class=\"icon-fascia-1\"><use xlink:href=\"" + iconLink + "\"></use></svg> </div> <p class=\"mt-2 text-white\">" + element.properties.title.toUpperCase() + " </p></a></div>";

                });
                fasciaUnoHtml += "</div>";
                $("#fascia_uno").html(fasciaUnoHtml);


                let linkFasciaDue = [];
                if (result.components.link_fascia_due != undefined) {
                    linkFasciaDue = result.components.link_fascia_due.components;
                }
                var fasciaDueHtml = "";
                if (linkFasciaDue.length > 0) {
                    fasciaDueHtml = "<div class=\"row justify-content-around\">\n";
                }
                linkFasciaDue.forEach(function (element) {
                    var iconLink = "";
                    if (element.properties.icon.indexOf("it-") != -1) {
                        iconLink = "svg/sprite.svg#" + element.properties.icon;
                    }
                    if (element.properties.icon.indexOf("ba-") != -1) {
                        iconLink = "svg/ba-sprite.svg#" + element.properties.icon;
                    }



                    var urlParams = new URLSearchParams(element.properties.url);

                    if (urlParams.has("section_id")) {
                        localStorage.setItem(urlParams.get("section_id") + "label", element.title);

                    }

                    fasciaDueHtml += "<div class=\"col text-center align-content-center\"> <a href=\"" + element.properties.url + "\" class=\"text-fascia-due\"><div class =\"box-fascia-2 text-center\"><svg class=\"icon-fascia-2\"><use xlink:href=\"" + iconLink + "\"></use></svg> </div> <p class=\"mt-2 text-fascia\">" + element.properties.title.toUpperCase() + " </p></a></div>";
                });
                fasciaDueHtml += "</div>";
                $("#fascia_due").html(fasciaDueHtml);

                let linkFasciaTre = [];
                if (result.components.link_fascia_tre != undefined) {
                    linkFasciaTre = result.components.link_fascia_tre.components;
                }

                var fasciaTreHtml = "";
                if (linkFasciaTre.length > 0) {
                    fasciaTreHtml = "<div class=\"row justify-content-around\">";
                }
                linkFasciaTre.forEach(function (element) {
                    var iconLink = "";
                    if (element.properties.icon.indexOf("it-") != -1) {
                        iconLink = "svg/sprite.svg#" + element.properties.icon;
                    }
                    if (element.properties.icon.indexOf("ba-") != -1) {
                        iconLink = "svg/ba-sprite.svg#" + element.properties.icon;
                    }

                    var urlParams = new URLSearchParams(element.properties.url);

                    if (urlParams.has("section_id")) {
                        localStorage.setItem(urlParams.get("section_id") + "label", element.title);

                    }

                    fasciaTreHtml += "<div class=\"col text-center align-content-center\"> <a href=\"" + element.properties.url + "\" class=\"text-fascia-tre\"><div class =\"box-fascia-3 text-center\"><svg class=\"icon-fascia-3\"><use xlink:href=\"" + iconLink + "\"></use></svg> </div> <p class=\"mt-2 text-fascia\">" + element.properties.title.toUpperCase() + " </p></a></div>";
                });
                fasciaTreHtml += "</div>";
                $("#fascia_tre").html(fasciaTreHtml);
                let linkGroup = [];
                if (result.components.link_group != undefined) {
                    linkGroup = result.components.link_group.components;
                }

                var linkGroupAreaHtml = "";
                linkGroup.forEach(function (element) {
                    linkGroupAreaHtml += "<div class=\"col-12 col-sm-12 col-md\">\n";
                    linkGroupAreaHtml += '<h6 class="dark-obj-name">' + element.title + '</h6>';
                    localStorage.setItem(element.id + "label", element.title);

                    if (element.components != null) {
                        let subLinks = element.components.link;
                        if (subLinks.components != undefined) {
                            if (subLinks.components.length > 0) {
                                linkGroupAreaHtml += "<hr class=\"border-link-group-area mr-4\"/><div class=\"link-list-wrapper dark\"><ul class=\"footer-list link-list clearfix\">";
                                subLinks.components.forEach(function (subEl) {
                                    linkGroupAreaHtml += "<li><a class=\"list-item text-dark\" href=\"" + subEl.properties.url + "\" title=\"Vai alla pagina:" + subEl.title + "\">" + subEl.title + "</a> </li>";
                                });
                                linkGroupAreaHtml += "</div></ul>";
                            }
                        }

                    }
                    linkGroupAreaHtml += "</div>";
                });
                $("#link_group_area").html(linkGroupAreaHtml);
            }
        }
    });

    var url_news_to_call = apibase.baseurl + site_config.newsOType;

    $.ajax({
        url: url_news_to_call, success: function (result) {
            if (result != null && result.content.length > 0) {
                var news_slider_html = "";
                var allNews = result.content;
                $('#news').html('<div id="newsSlider" class="owl-carousel "></div>');
                allNews.forEach(function (news) {
                    news_slider_html += '<div class="item row  mt-0 mr-3 ml-3">';
                    var immagine = {};
                    var address = "";
                    var htmlImg = "";
                    var colNews = "col-12";

                    if (news.components != null && news.components.immagine.components.length > 0) {
                        immagine = news.components.immagine.components[0];
                        address = immagine.components.file.components[0].properties.address;
                        htmlImg = '<img class="img_elenco" src="' + baseImgUrl + address + '"" /> ';
                    }
                    var linkNews = "./notizia.html?ref_type_id=" + site_config.newsOType + "&detail_obj_id=" + news.id;
                    var stringedDate = StringRenderer.renderDataTestuale(news.properties.data_pubblicazione);
                    news_slider_html += '<div class="' + colNews + '"><div class=""><div class="medium-font-size"><hr class="border-news pr-2 pl-2"/><div class="text-center">' + htmlImg + ' </div><div><p class="text-font mt-2 mb-0">' + stringedDate + '</p></div> <h6 >' + news.title + '</h6><p class="text-font">' + news.properties.intestazione + '</p> <a class="read-more" href="' + linkNews + '"><span class="text">Leggi di più</span><svg class="icon"><use xlink:href="svg/sprite.svg#it-arrow-right"></use></svg></a></div></div></div></div>';
                });
                $("#newsSlider").append(news_slider_html);
                // $("#eventsSlider").append(news_slider_html);

                var owl = $("#newsSlider");
                owl.owlCarousel({
                    responsiveClass: true,
                    responsive: {
                        0: {
                            items: 1,
                            nav: false,
                            loop: true
                        },
                        600: {
                            items: 1,
                            nav: false
                        },
                        1000: {
                            items: 4,
                            nav: false,
                            loop: false
                        }
                    },
                    navigation: true
                });


            }
        }
    });

    var url_evento_call = apibase.baseurl + site_config.eventOtype;

    $.ajax({
        url: url_evento_call, success: function (result) {
            if (result != null && result.content.length > 0) {
                var news_slider_html = "";
                var allNews = result.content;
                $('#events').html('<div id="eventsSlider" class="owl-carousel "></div>');
                allNews.forEach(function (news) {
                    news_slider_html += '<div class="item row  mt-0 mr-3 ml-3">';
                    let immagine = {};
                    var address = "";
                    var htmlImg = "";
                    var colNews = "col-12";

                    if (news.components != null && news.components.immagine != undefined) {
                        if (components.immagine.components.length > 0) {
                            immagine = news.components.immagine.components[0];
                            address = immagine.components.file.components[0].properties.address;
                            htmlImg = '<img class="img_elenco" src="' + baseImgUrl + address + '" />';
                        }

                    }
                    var linkNews = "./notizia.html?ref_type_id=" + news.typeId + "&detail_obj_id=" + news.id;
                    var stringedDate = "";
                    if (news.properties.data_ora_pubblicazione != undefined) {
                        stringedDate = StringRenderer.renderDataTestuale(news.properties.data_ora_pubblicazione);
                    } else {
                        stringedDate = StringRenderer.renderDataTestuale(news.lastModifiedDate);

                    }
                    news_slider_html += '<div class="' + colNews + '"><div class=""><div class="medium-font-size"><div class="text-center">' + htmlImg + ' </div><div><hr class="border-news pr-2 pl-2"/><p class="text-font mt-2 mb-0">' + stringedDate + '</p></div> <h6 >' + news.title + '</h6><p class="text-font">' + news.properties.descrizione + '</p> <a class="read-more" href="' + linkNews + '"><span class="text">Leggi di più</span><svg class="icon"><use xlink:href="svg/sprite.svg#it-arrow-right"></use></svg></a></div></div></div></div>';
                });
                $("#eventsSlider").append(news_slider_html);
                // $("#eventsSlider").append(news_slider_html);

                var owl = $("#eventsSlider");
                owl.owlCarousel({
                    responsiveClass: true,
                    responsive: {
                        0: {
                            items: 1,
                            nav: false,
                            loop: true
                        },
                        600: {
                            items: 1,
                            nav: false
                        },
                        1000: {
                            items: 2,
                            nav: false,
                            loop: false
                        }
                    },
                    navigation: true
                });

                var owl2 = $("#eventsSlider");
                owl2.owlCarousel({
                    items: 1,
                    navigation: true
                })
            }
        }
    });


    loadNews();
    window.setInterval(function () {
        loadNews();
    }, 120000);

    renderDiretta();

});

function loadNews() {
    var url_ci_news_to_call = apibase.baseurl + "10500?size=4";

    $.ajax({
        method: 'POST',
        data: JSON.stringify({
            approfondimento: "F",
            "SORT": [{
                "direction": "DESC",
                "property": "data_pubblicazione"
            }, {
                "direction": "DESC",
                "property": "data_pubblicazione_time"
            }]
        }),
        contentType: 'application/json',
        dataType: 'json',
        url: url_ci_news_to_call, success: function (result) {
            if (result != null && result.content.length > 0) {
                console.log('update from consiglio informa');
                var news_slider_html = "";
                var allNews = result.content;
                allNews.forEach(function (news) {
                    news_slider_html += "";
                    var linkNews = news.properties.url;
                    if (linkNews == null) {
                        linkNews = './consiglioinforma/notizia.html?id=' + news.id;
                    }
                    var stringedDate = StringRenderer.renderDataTestuale(news.properties.data_pubblicazione) + ', ' + news.properties.data_pubblicazione_time;
                    news_slider_html += '<div class="col-12 col-lg-6"> <div class="bg-white p-1 mt-2"><p class="mt-2 ml-2 mb-0 "> <svg class="icon icon-dark"> <use xlink:href="svg/ba-sprite.svg#ba-news"></use></svg><span class="data ml-1">' + stringedDate + '</span></p><h6 class="ml-3"><a class="dark-obj-name-bold" href="' + linkNews + '" >' + news.properties.title + '</a></h6></div></div>';
                });
                if (news_slider_html != "") {
                    $("#row_consiglio_informa").html("");
                    $("#row_consiglio_informa").append(news_slider_html);
                }
            }
        }
    });
}

function renderDiretta() {
    $.ajax({
        url: apibase.baseurl + '/10535',
        success: function (result) {
            if (result != null && result.content.length > 0 &&
                result.content[0].properties.stato_della_diretta == 'ONLINE') {
                $('#direttaOffline').hide();
                $('#direttaOnline').removeClass("d-none");
            }
        }
    });
}

